const dtFormat = require('date-fns/format')
const numeral = require('numeral')

const formatters = []
formatters.date = list => (format, value, next) => {
  if (!list[format]) return next()
  return dtFormat(format, value)
}

formatters.number = list => (format, value, next) => {
  if (!list[format]) return next()
  return numeral(value).format(format)
}

/**
 * Creates a Formatting function that can be used by i18next formatting
 * @param {Object} description contains object with formatting rules in format of { [type]: 'formatting text' }
 * @returns {Function} returns a composed formatting function
 */
export default description => {
  const rules = Object.keys(description || {})

  for (let rule of rules) {
    if (formatters[rule]) formatters.push(formatters[rule](description[rule]))
    else console.warn(`front-hub: ${rule} was not implemented`)
  }

  return (format, value) => {
    let x = -1
    const next = () => formatters[++x] && formatters[x](format, value, next)
    try {
      return next()
    } catch (e) {
      console.error(e)
    }
  }
}
