/* eslint-disable import/prefer-default-export */
import { application, services } from '../../../core/backbone'
import fronthubServiceConfiguration from 'front-hub-service-configuration'

import microFrontendProduction from './hub'

import {
  rbac,
  name,
  version,
  events,
  publicPath,
} from 'front-hub-application-host-configuration-prod'

import { changeVirtualPath } from 'front-hub-context-api'
import FronthubError from '../../../core/helpers/FronthubError'

const serviceList = services()
const microfrontend = application()
;(fronthubServiceConfiguration || [])
  .map(service => {
    Object.defineProperty(service, 'endpoint', {
      get() {
        if (service.endpoints === undefined) {
          throw new FronthubError(
            `Service '${service.name}' must have the endpoints property as an object with the production URL.`,
          )
        }
        return service.endpoints.production
      },
    })
    return service
  })
  .forEach(item => serviceList.push(item))

export const Connect = Component => {
  if (typeof microfrontend.connect !== 'function') {
    const message =
      'front-hub: private connect function was not defined before public Connect call'
    throw new FronthubError(message)
  }
  return microfrontend.connect(fronthubContext => {
    //legacy support for require@1.0.2
    if (!fronthubContext.configuration)
      fronthubContext.configuration = fronthubContext
    const configuration = fronthubContext.configuration
    configuration.rbac = rbac
    configuration.events = events
    configuration.name = name
    configuration.version = version
    typeof publicPath === 'function' &&
      changeVirtualPath(publicPath(configuration))
    return microFrontendProduction(Component, fronthubContext)
  })
}
