import React, { lazy, useState } from 'react'
import styled from 'styled-components'
import { Theme, Reset, Button } from '@resultadosdigitais/tangram-components'
import { Connect } from '@resultadosdigitais/front-hub/react'
import './App.css'

import Welcome from './components/Welcome'

const Root = styled.div`
  background: #212121;
  color: var(--color-white);
  font-family: var(--text-font-family);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`

const Count = lazy(() =>
  import(
    /* webpackChunkName: "count", webpackPreload: true */ './components/Count'
  ),
)

const App = () => {
  const [load, setLoad] = useState()

  return (
    <Theme value={Theme.kinds.lina}>
      <Reset />
      <Root>
        <Welcome />
        <React.Suspense fallback={<div>Loading...</div>}>
          <Button onClick={() => setLoad(true)}>Load Counter</Button>
          {load && <Count />}
        </React.Suspense>
      </Root>
    </Theme>
  )
}

export default Connect(() => <App />)
